* {
  box-sizing: border-box;
}
img {
  width: 100%;
  border-radius: 5px;
}
@media screen and (max-width: 599px) {
  img {
    height: 250px;
    object-fit: cover;
 }
}
body {
  background: #eaebe5;
  color: white;
  margin: 0;
  padding: 0;
}
main {
  padding: 1rem;
  max-width: 600px;
  margin: auto;
}
h1 {
  font-family: cursive;
  color: black;
  margin-bottom: 0px;
}
h2 {
  color: #36ad36;
  font-family: cursive;
  font-weight: 500;
  margin-top: 0px;
}
h3 {
  font-family: cursive;
  font-weight: 500;
  margin: 0.5rem 0;
  color: #424242;
}
@media screen and (min-width: 600px) {
  .grid-wrapper {
    display: grid;
    grid-auto-flow: column;
    grid-gap: 1rem;
 }
}
@media screen and (min-width: 1025px) {
  .grid-wrapper {
    grid-template-columns:  2fr;
 }
}
@media screen and (min-width: 600px) and (max-width: 1024px) {
  .grid-wrapper {
    grid-template-columns: 1fr;
 }
}
article {
  padding-bottom: 1rem;
}
article p {
  font-family: cursive;
  font-size: 0.95rem;
  color: #626262;
  padding-bottom: 1rem;
  border-bottom: 1px solid #4d4d4d;
}

article:nth-child(4) {
  grid-area: 1/2/4/3;
}
@media screen and (min-width: 1025px) {
  article:nth-child(8) {
    grid-area: 1/4/4/-1;
 }
}
@media screen and (min-width: 600px) and (max-width: 1024px) {
  article:nth-child(8) {
    grid-area: 4/2/8/3;
 }
}
@media screen and (min-width: 1025px) {
  article:nth-child(4n+3) {
    border-bottom: 0;
 }
}
@media screen and (min-width: 1025px) {
  article:nth-child(4) {
    border-right: 1px solid #4d4d4d;
    padding-right: 1rem;
 }
}
@media screen and (min-width: 600px) {
  article:nth-child(4n) {
    border-left: 1px solid #4d4d4d;
    border-bottom: 0;
    padding-left: 1rem;
 }
}
.footer-note {
  text-align: center;
  font-family: 'Karla', cursive;
  font-size: 0.9rem;
  background: white;
  padding: 1rem;
  color: black;
  border-radius: 5px;
}
.footer-note a {
  color: inherit;
  font-weight: 600;
}

.carousel .control-dots .dot {
  background: #36ad36 !important;
}

.carousel.carousel-slider .control-arrow {
  color: #36ad36 !important;
  height: 250px !important;
  background: transparent !important;
  
}

.carousel .control-prev.control-arrow:before {
  border-right: 8px solid black !important;
}

.carousel .control-next.control-arrow:before {
  border-left: 8px solid black !important;
}